/* Scrollbar */

/* width */
::-webkit-scrollbar {
  width: 20px;
  background: rgb(219, 217, 217);
}

/*button*/
/* ::-webkit-scrollbar-button {
  width: 5px;
  height: 5px; 
} */

/* Track */
::-webkit-scrollbar-track {
  /* background: green; */
  /* --webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); */
  /* border-left: 9px solid #f6f6f6; */
  margin-top: 10px;
  margin-bottom: 10px;
  background: rgb(219, 217, 217);
  border-left: 9px solid rgb(219, 217, 217);
  border-right: 9px solid rgb(219, 217, 217);
}

/* Handle */
::-webkit-scrollbar-thumb {
  /* --webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5); */
  background: #949191;;
  border-radius: 8px;
  border-left: 5px solid rgb(219, 217, 217);
  border-right: 5px solid rgb(219, 217, 217);
}
::-webkit-scrollbar-thumb:hover {
  /* --webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5); */
  background: rgb(104, 102, 102);
}

