.checkout {
  display: flex;
  padding: 20px;
  background-color: white;
  height: -webkit-max-content;
  height: max-content;
}
.dark_checkout {
  display: flex;
  padding: 20px;
  background-color: #231f1e;
  height: -webkit-max-content;
  height: max-content;
}

.checkout__ad {
  width: 100%;
  margin-bottom: 10px;
}
.dark_checkout__ad {
  width: 98%;
  margin-bottom: 10px;
}

.checkout__title {
  margin-right: 10px;
  padding: 10px;
  border-bottom: 2px solid lightgray;
  font-weight: bolder;
  /* font-family: 'Balsamiq Sans', cursive;} */
  /* font-family: 'Balsamiq Sans', cursive; */
  font-family: 'PT Sans', sans-serif;
}
.dark_checkout__title {
  margin-right: 10px;
  padding: 10px;
  border-bottom: 2px solid rgb(85, 85, 85);
  color: white;
  font-weight: bolder;
  /* font-family: 'Balsamiq Sans', cursive;} */
  /* font-family: 'Balsamiq Sans', cursive; */
  font-family: 'PT Sans', sans-serif;
}


@media screen and (max-width: 998px) {
  .checkout {
    display: flex;
    padding: 20px;
    background-color: white;
    height: -webkit-max-content;
    height: max-content;
    /* min-height: 100vh; */
    flex-wrap: wrap;
  }
  .dark_checkout {
    display: flex;
    padding: 20px;
    background-color: #231f1e;
    height: -webkit-max-content;
    height: max-content;
    /* min-height: 100vh; */
    flex-wrap: wrap;

  }

  .checkout__ad {
    width: 98%;
    margin-bottom: 10px;
}
.dark_checkout__ad {
  width: 98%;
  margin-bottom: 10px;
}

.checkout__title {
  margin-right: 5px;
  padding: 5px;
  border-bottom: 2px solid lightgray;
  font-weight: bolder;
  font-family: 'PT Sans', sans-serif;
  font-size: 18px;
}
.dark_checkout__title {
  margin-right: 5px;
  padding: 5px;
  border-bottom: 2px solid rgb(90, 90, 90);
  font-weight: bolder;
  font-family: 'PT Sans', sans-serif;
